import {
  Component,
  Input,
  Output,
  EventEmitter,
  OnInit,
  OnChanges,
  SimpleChanges
} from '@angular/core';
import { define } from 'skatejs';
import { default as PfHello } from '../web-components/pf-hello/pf-hello.component';

define(PfHello);

@Component({
  selector: 'pf-hello-component',
  template: require('./hello.component.html'),
  styles: [require('./hello.component.css')]
})
export class HelloComponent implements OnChanges, OnInit {
  ngOnInit(): void {
    console.log('on init, user component');
  }
  ngOnChanges(changes: SimpleChanges): void {
    console.log(changes);
  }
  /**
   * The Tooltip placement
   */
  @Input() name: string;

  /**
   * The slot description
   */
  @Input() description: string;

  constructor() {}
}



